import { Col, Container, Row } from 'react-bootstrap';

import './all-inclusive-body.css';

interface props {
  allInclusiveDetails: any[];
}

function AllInclusiveBody({ allInclusiveDetails }: props) {
  return (
    <div className="restaurants-bar-body-div pt-5 pb-5">
      <Container>
        <Row className="justify-content-md-center text-center mb-5">
          <Col lg="8">
            <h1>Detalles Todo Incluido</h1>
          </Col>
        </Row>
        <Row className="justify-content-md-center text-left mb-5">
          <Col lg="10">
            <ul className="default-list">
              {allInclusiveDetails.map((value, i) => {
                return (
                  <li key={i}>
                    <i className="fa fa-check-circle"></i> {value}
                  </li>
                );
              })}
            </ul>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default AllInclusiveBody;
